import React, { useState, useEffect } from "react";

import styles from "./CollectionGroup.module.css";

import { Collection } from "../../../../components";
import axios from "axios";

const CollectionGroup = () => {
  const [retrievedCollections, setRetrievedCollections] = useState([]);

  useEffect(() => {
    const fetchCollection = async () => {
      let result = await axios.get("http://localhost:5000/collections");

      setRetrievedCollections(result.data);
    };

    fetchCollection();
  }, []);

  console.log(retrievedCollections);
  return (
    <div>
      {retrievedCollections.map((collection) => (
        <Collection collection={collection} />
      ))}
    </div>
  );
};

export default CollectionGroup;
