import React from "react";

import styles from "./Footer.module.css";

import { TextInput } from "../../components";

const Footer = (props) => {
  const { imageSource } = props;
  return (
    <div className={styles.footer}>
      <div className={styles.footer_lookbook}>
        <h3 className={styles.footerText}>LOOKBOOK</h3>
        <a>F/W 2020</a>
        <a>S/S 2021</a>
        <a>F/W 2021</a>
      </div>
      <div className={styles.footer_nxt}>
        <h3 className={styles.footerText}>NXT</h3>
        <a>WHO WE ARE</a>
        <a>CONTACT US</a>
      </div>
      <div className={styles.footer_findus}>
        <h3 className={styles.footerText}>FIND US</h3>
        <a>INSTAGRAM</a>
        <a>TWITTER</a>
        <a>FACEBOOK</a>
      </div>

      <div className={styles.footer_form}>
        <form className={styles.footerform} action="/newsletter" method="POST">
          <h3>Join our newsletter:</h3>

          <input
            className={styles.inputField}
            name="email"
            type="text"
            placeholder="Email"
            autoComplete="off"
          />

          <button type="submit" className={styles.footerButton}>
            JOIN
          </button>
        </form>
      </div>
    </div>
  );
};

export default Footer;
