import React from "react";

import styles from "./About.module.css";

import { motion } from "framer";

const About = () => {
  return (
    <motion.div
      className={styles.aboutContainer}
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 1 }}
    >
      <h1>About Us</h1>

      <div className={styles.aboutContent}>
        <div className={styles.aboutLogo}>LOGO</div>
        <p className={styles.description}>
          We are just two people who want to put out some cool stuff. We're
          located in the suburbs of Chicago.
        </p>
        <p className={styles.description}>
          <strong>Tommy</strong> - Just a kid who likes streetwear, anime, and
          gaming
        </p>
        <p className={styles.description}>
          <strong>Nikki</strong> - I love to create clothing and be myself
        </p>
      </div>
    </motion.div>
  );
};

export default About;
